<template lang="en">
    <div>
        <div class="my-popups modal right fade show" style="display:block;">
            <div class="modal d-block ">
                <div class="modal-dialog doc-setup-slide mw-1100" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{HeaderText}}</h5>
                            <button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close">
                                <i class="fa fa-times" aria-hidden="true"></i>
                            </button>
                        </div>
                        <div class="modal-body bg-white initialsetup_newtab_mainbase newtabdes_buttonsbase">
                            <section class="container-fluid p-0" v-if="TabList.length > 0">
                                <div class="left-menu-tab new_tab">
                                    <div class="col-md-12 pb-0">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                                <div class="row">
                                                    <div class="col-8 pr-0 font-weight600 mb-1">{{ $t('CompleteYourProfile') }}</div>
                                                    <div class="col-4 text-right">
                                                        <b class="">
                                                            <span class="smallresult" v-if="setupProgresText !=='Completed'">Progress {{currentTabIndex}} of {{TabList.length}}</span></b>
                                                            <span class="smallresult" v-if="setupProgresText=='Completed'">
                                                                <i class="fa fa-check-circle" v-bind:class="{'text-success':setupProgresText=='Completed', 'text-muted':setupProgresText!=='Completed'}"></i>
                                                                {{$t(setupProgresText)}}
                                                            </span>
                                                        <!-- <b id="divConsumendStorage">
                                                            <span class="smallresult">{{GetLocalizedValue('Progress')}} {{stepOfTabs-1}} of {{TotalTabs}}</span>
                                                        </b> -->
                                                    </div>

                                                    <div class="col-4 pl-0 text-right">
                                                        
                                                    </div>
                                                </div>
                                                <div class="initialsetup_newtab_design progress storagegraph smaller">
                                                    <div class="progress-bar progress-bar-primary px-0 text-right memoryused"
                                                        id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${setupProgress}%; font-weight:500; font-size:14px`"
                                                        :title="setupProgress">
                                                        <!-- {{ setupProgress }}% -->
                                                    </div>
                                                </div>
                                                <!--<div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                                     <ul>
                                                        <li>
                                                            <b id="divConsumendStorage"><span>Progress {{currentTabIndex}} of {{TabList.length}}</span></b>
                                                        </li>
                                                    </ul>
                                                </div> -->
                                            </div>
                                        </div>  
                                    </div>
                                    <div class="mt-3" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                                        <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix initialsetup_newtab_design">
                                            <li :class="{'nav-item FirstScreen': true, 'active': item.tabCode == currentSelectedTab.tabCode, 'initialsetup_completed': item.isCompleted }" v-for="(item, index) in TabList" v-on:click="setCurrectTab(item)">
                                                <a class="digits_num">{{(index+1)}}.</a>
                                                <a :class="{'nav-link': true}"  style="cursor: pointer;">
                                                    <i class="fa fa-check-circle text-success" v-if="item.isCompleted"></i>
                                                    <i :class="'pr-2 text-muted ' + item.icon" v-if="!item.isCompleted"></i>
                                                    <span>{{ $t(item.displayName) }}</span>
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                <div class="right-content">
                                    <div class="tab-content">
                                        <div id="step1" class="tab-pane call-detail fade p-0 border-0 active show " v-if="currentSelectedTab != null">
                                            <moduleSetupBridge :component="currentSelectedTab.componentName" :moduleCode="moduleCode" v-on:PreviousTab="PreviousTab" v-on:UpdateProgress="UpdateProgress" v-on:NextTab="NextTab" v-on:Close="Close" :tabCount="TabList.length" :currentTabIndex="currentTabIndex"></moduleSetupBridge>
                                        </div>
                                    </div>
                                </div>
                            </section>
                            <section class="container-fluid p-0" v-if="TabList.length == 0">
                                <div class="text-danger text-center p-4">
                                    {{$t("NoRecordfound")}}
                                </div>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<!-- <style>
    .mandatory {
        font-size: 11px;
    }
</style> -->
<script>
    import moduleSetupBridge from "./moduleComponents/moduleSetupBridge.vue"
    export default {
        props: {
            moduleCode: '',
            moduleDisplayName: "",
            setupTabs: []
        },
        watch:{
            setupTabs: function(val){
                this.TabList = val;
                if(val.length > 0){
                    this.currentSelectedTab = val[0];
                }
            }
        },
        components: {
            moduleSetupBridge
        },
        data: function(){
            return {
                TabList: [],
                currentSelectedTab: null,
                setupProgress: 0,
                setupProgresText: 'Inprogress',
                progressBarColor: "",
                HeaderText : ''
            }
        },
        created: function() {
            this.HeaderText= this.moduleCode.toLowerCase() == 'company' ? 'Initial Setup' : 'Setup of ' + this.$t(this.moduleDisplayName);
            this.TabList = this.setupTabs;
            if(this.setupTabs.length > 0){
                this.currentSelectedTab = this.setupTabs[0];
            }
            this.GetProgresSetupStatusDetail();
        },
        computed: {
            currentTabIndex: function(){
                return (this.TabList.indexOf(this.currentSelectedTab) + 1)
            }
        },
        methods: {
            GetProgresSetupStatusDetail: function(){
                var ths = this;
                var weightage = (100 / ths.setupTabs.length).toFixed(2);
                ths.setupTabs.forEach(function(item){
                    if(item.isCompleted == true){
                        if((ths.setupProgress + weightage) < 100){
                            ths.setupProgress += weightage;
                        } else{
                            ths.setupProgress = 100;
                        }
                        if(ths.setupProgress == 100){
                            ths.setupProgresText = 'Completed';
                            ths.progressBarColor = "bg-success";
                        }
                    }
                })
            },
            // GetTabLists: function(){
            //     var ths = this;
            //     GetSetupModuleTabList(ths.moduleCode).then(function(response){
            //         ths.currentSelectedTab = response[0];
            //         ths.TabList = response;
            //     });                
            // },
            setCurrectTab: function(item, index) {
                var ths = this;
                if(typeof(item) != 'undefined'){
                    ths.currentSelectedTab = item;
                }
            },
            NextTab: function(){
                var ths = this;
                var index  = this.TabList.indexOf(ths.currentSelectedTab)
                if(index > -1 && this.TabList.length > (index + 1)) {
                    ths.currentSelectedTab = this.TabList[index+1];
                }
                this.GetProgresSetupStatusDetail();
            },
            UpdateProgress: function(index, status){
                this.TabList[(index-1)].isCompleted = true;
                this.$emit("UpdateProgress", index, completeStatus);
                this.GetProgresSetupStatusDetail();
            },
            PreviousTab: function(){
                var ths = this;
                var index  = this.TabList.indexOf(ths.currentSelectedTab);
                if(index > -1 && this.TabList.length >= (index + 1)) {
                    ths.currentSelectedTab = this.TabList[index - 1];
                }
            },
            Close: function(){
                this.$emit("close");
            }
        },

    }
</script>
                    