<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid padding-t_10 px-0">
            <div class="left-menu-tab new_tab nosrcoll">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ $t('Settings') }}</span>
                    </div>
                </div>
                <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                    <div class="input-group customsearchbox">
                        <input type="text" maxlength="50" placeholder="Search by Name" v-model="moduleSearchElement"
                            class="form-control searchcustomfilter border-right-0" autocomplete="off" v-on:keyup="OnModuleSearch" />
                        <div class="input-group-append">
                            <span class="input-group-text bg-transparent bg-white border-left-0">
                                <i class="fa fa-search"></i>
                            </span>
                        </div>
                    </div>
                </div>
                <div class="mt-2 mCustomScrollbar _mCS_4">
                    <a class="card mobile-tab-accordian active" href="javascript:;" data-controllername="billingrule" data-actionname="ImportFormatListing" v-on:click="showMobileTabs = !showMobileTabs;">
                        <div class="card-header" role="tab">
                            <h5><span class="mb-0"><i :class="getModuleIcon(GetSelectedModuleItem.module_name)"></i> {{ $t(GetSelectedModuleItem.display_name) }}<i
                                        class="fa fa-angle-down rotate-icon float-right" aria-hidden="true"></i></span>
                            </h5>
                        </div>
                    </a>


                    <div v-if="showMobileTabs">
                        <a v-for="item in moduleList" v-if="GetSelectedModuleItem.module_name != item.module_name" v-on:click="SetCurrentTab(item.module_name, item.display_name); showMobileTabs = false;" class="card mobile-tab-accordian" href="javascript:;" data-controllername="billingrule" data-actionname="ImportFormatListing">
                            <div class="card-header" role="tab">
                                <h5><span class="mb-0"><i :class="getModuleIcon(item.module_name)"></i> {{ $t(item.display_name) }}
                                    <i class="fa fa-angle-down rotate-icon float-right" aria-hidden="true"></i></span>
                                </h5>
                            </div>
                        </a>
                    </div>



                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix">
                        <li v-for="item in moduleList" v-if="item.isVisible == true">
                            <a checkprivilege="Yes"
                                :class="{'nav-link left-menu': true, 'active':(currentTab == item.module_name.toLowerCase()) }"
                                data-toggle="tab" role="tab" :aria-controls="item.module_name" aria-selected="false"
                                :data-text="item.module_name" :data-tabr="'#' + item.module_name"
                                v-on:click="SetCurrentTab(item.module_name, item.display_name)" href="javascript:;">
                                <i :class="getModuleIcon(item.module_name)" aria-hidden="true"></i>{{ $t(item.display_name) }}
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0">
                    <div class="tab-content">
                        <!-- <div class="expand-list">
                            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                            </a>
                        </div> -->
                        <section class="page-action action-margin tablehead mobile_d-none">
                            <div class="col-md-12 p-0">
                                <div class="theme-primary partition  p-actions-expand">
                                    <span id="titleName" class="p-name text-white"
                                        style="text-transform: capitalize;">{{ $t('Settings') + ' > ' +
                                        $t(moduleDisplayName)
                                        }}</span>
                                </div>
                            </div>
                        </section>                        
                        <template  v-if="!unauthorized">
                            <div class="tab-pane call-detail fade p-0 active show border pb-3"
                            v-if="currentTab.toLowerCase() != 'configuration'">
                                <div class="mx-3">
                                    <ul class="dashbord-widget py-3">
                                        <li class="cursor-pointer one_wid" style="cursor: pointer !important;"
                                            v-on:click="CloseOpenSetupModule">
                                            <span class="barprogress">
                                                <div class="Task">
                                                    <div class="c100 small"
                                                        v-bind:class="[progressColor, ('p' + setupProgress)]">
                                                        <span>{{setupProgress}}%</span>
                                                        <div class="slice">
                                                            <div class="bar"></div>
                                                            <div class="fill"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </span>
                                            <h3 style="width: 65%;">{{ $t('Setup') }} <br> <span>Setup the module in a few
                                                    steps</span></h3>
                                            <i aria-hidden="true" class="fa fa fa-cog"></i>
                                        </li>
                                        <!-- <li class="cursor-pointer two_wid" style="cursor: pointer !important;">
                                            <h3>{{ $t('GeneralSettings') }}<br><span>Configure core system options</span></h3> <i aria-hidden="true"
                                                class="fa fa fa-sliders"></i>
                                        </li> -->
                                        <li class="cursor-pointer two_wid" style="cursor: pointer !important;"
                                            v-on:click="CloseOpenConfigPopup">
                                            <h3>{{ $t('Configuration') }}<br><span>Configure the module as per the
                                                    requirements</span></h3> <i aria-hidden="true"
                                                class="fa fa fa-wrench"></i>
                                        </li>
                                        <!-- <li class="cursor-pointer four_wid" style="cursor: pointer !important;">
                                            <h3>{{ $t('EnableDisableSection') }}<br><span>Controls the visibility of sections on this page</span></h3> <i aria-hidden="true"
                                                class="fa fa fa-puzzle-piece"></i>
                                        </li> -->
                                    </ul>
                                    <div style="clear: both;"></div>
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span class="spngropuname">
                                                {{ $t('MoreSettings') }}
                                            </span>
                                        </h5>
                                    </div>
                                </div>
                                <ul class="shortcuts-menu pb-3 connectedSortable advancedsetup"
                                    v-if="moreSettings.length > 0">
                                    <li class="position-relative clients" v-for="(item,index) in moreSettings"
                                        target="_blank" :key="index">
                                        <a onmousedown="return false;" :href="item.redirectTo + '?reqUrl='+currentUrl"
                                            data-id="37" data-fav_id="48522" data-order="1" data-displayname="Clients"
                                            data-toggle="tooltip" title="" data-placement="bottom" class="short-menu-item"
                                            data-toggle-tooltip="tooltip" data-original-title="Clients">
                                            <span class="left-icon-m">
                                                <em :class="'fa ' + item.icon"></em>
                                            </span>
                                            <span class="short-item-name">{{ $t(item.displayName) }}</span>
                                        </a>
                                    </li>
                                </ul>
                                <div v-if="moreSettings.length == 0" class="text-danger text-center p-4">
                                    {{$t("NoRecordfound")}}
                                </div>
                            </div>
                            <div class="tab-pane call-detail fade p-0 active pt-3 show border pb-3"
                                v-if="currentTab.toLowerCase() == 'configuration'">
                                <template v-for="item in generalSettingList">
                                    <div class="px-3">
                                        <div class="heading-border">
                                            <h5 class="h5">
                                                <span class="spngropuname">
                                                    {{ $t(item.groupName) }}
                                                </span>
                                            </h5>
                                        </div>
                                    </div>
                                    <ul class="shortcuts-menu pb-3 connectedSortable advancedsetup">
                                        <li class="position-relative clients" v-for="(link,index) in item.links"
                                            :key="index">
                                            <a onmousedown="return false;" data-toggle="tooltip" title=""
                                                data-placement="bottom" class="short-menu-item"
                                                data-toggle-tooltip="tooltip" :data-original-title="$t(link.displayName)"
                                                @click="RedirectTo(link)">
                                                <span class="left-icon-m">
                                                    <em :class="'fa ' + link.icon"></em>
                                                </span>
                                                <span class="short-item-name">{{ $t(link.displayName) }}</span>
                                            </a>
                                        </li>
                                    </ul>
                                    <div style="clear:both"></div>
                                </template>
                            </div>
                        </template>
                        <div v-if="unauthorized" class="text-danger text-center p-4">
                            {{ $t("NotAuthorizedSection") }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <configuration v-if="isOpenConfiguration" :moduleCode="currentTab"  :moduleDisplayName="moduleDisplayName" @close="CloseOpenConfigPopup">
        </configuration>
        <moduleSetupPopup v-if="isOpenSetupModule" :moduleCode="currentTab" :moduleDisplayName="moduleDisplayName" :setupTabs="setupTabs"
            @close="CloseOpenSetupModule"></moduleSetupPopup>
    </section>
</template>
<style>
    .left-icon-m i {
        font-size: 33px;
    }
    .shortcuts-menu li:nth-child(9n+1) .left-icon-m i {
        color: #216bca;
    }
    .shortcuts-menu li:nth-child(9n+2) .left-icon-m i {
        color: #63983e;
    }
    .shortcuts-menu li:nth-child(9n+3) .left-icon-m i {
        color: #575b6f;
    }
    .shortcuts-menu li:nth-child(9n+4) .left-icon-m i {
        color: #43a38a;
    }
    .shortcuts-menu li:nth-child(9n+5) .left-icon-m i {
        color: #cc7d38;
    }
    .shortcuts-menu li:nth-child(9n+6) .left-icon-m i {
        color: #735da5;
    }
    .shortcuts-menu li:nth-child(9n+7) .left-icon-m i {
        color: #7e6c5e;
    }
    .shortcuts-menu li:nth-child(9n+8) .left-icon-m i {
        color: #e3644b;
    }
    .shortcuts-menu li:nth-child(9n+9) .left-icon-m i {
        color: #dcad3a;
    }
    .shortcuts-menu li{
        max-width: 25% !important;
    }
    .short-item-name{
        font-size: 16px;;
    }
    .dashbord-widget .cursor-pointer h3 {
        font-size: 16px;
        font-weight: 700;
        max-width: 100%;
    }
    .dashbord-widget li h3 span{
        font-size: 12px;
    }
    .dashbord-widget li {
        position: relative;
    }
    .dashbord-widget i {
        position: absolute;
        right: 16px;
        top: 30px;
    }
    .barprogress {
        position: relative !important;
        float: left;
        right: auto !important;
    }
    .c100:after {
        background-color: #edf7ff;
    }
</style>
<script>
import dateService from "@/services/DataService";
import configuration from './configurationPopup';
import moduleSetupPopup from './moduleSetupPopup';
import {GetMoreSettingsArray, generalSettings} from './schema'
var currentUrl = window.location.pathname;
export default {
    components: {
        configuration,
        moduleSetupPopup
    },
    data: function(){
        return {
            isLoading: true,
            userTypeId: 3,
            moduleList: [],
            currentTab: 'Appointment'.toLowerCase(),
            moduleDisplayName: "Appointment",
            moreSettings: [],
            setupProgress: 0,
            isOpenConfiguration: false,
            isOpenSetupModule: false,
            progressColor: 'progress-bar progress-bar-striped bg-default',
            setupTabs: [],
            generalSettingList: generalSettings,
            currentUrl: currentUrl,
            showMobileTabs: false,
            moduleSearchElement: "",
            unauthorized: false
        }
    },
    created: function() {        
        this.userTypeId = this.GetUserInfo.UserType == 'CA' ? 1 : this.GetUserInfo.UserType == 'PM' ? 2 : this.GetUserInfo.UserType == 'NU' ? 3 : 4;
        this.GetModuleList();
    },
    computed:{
         SetupProgresText(){
           return this.setupProgress==0?'Not Started':(this.setupProgress==100?'Completed':'In Progress');
         },
         GetSelectedModuleItem(){            
            var ths = this;
            var getModuleObj = ths.moduleList.filter((x) => { return x.module_name.toLowerCase() == ths.currentTab.toLowerCase()});
            return getModuleObj[0];
         },
         sortedModuleList() {
            return this.moduleList.sort((a, b) => {
                if (a.display_name === 'General') {
                    return -1; 
                }
                if (b.display_name === 'General') {
                    return 1; 
                }
                return 0; 
            });
        }
    },
    methods: {
        OnModuleSearch: function(){
            var ths = this;
            if(ths.moduleSearchElement != ""){
                ths.moduleList.forEach(function(x){                    
                    if(x.display_name.toLowerCase().includes(ths.moduleSearchElement.toLowerCase())){
                        x.isVisible = true;
                    } else{
                        x.isVisible = false;
                    }
                });
            } else {

                ths.moduleList.forEach(function(x){
                    x.isVisible = true;
                });
            }
        },
        GetProgresSetupStatusDetail: function(){
            
            var ths = this;
            ths.isLoading=true;
            dateService.CheckPrivilege(`controller=AdvanceSetup&action=${ths.currentTab}`).then(function(priv){                
                if(priv.data || ths.currentTab == "configuration"){
                    ths.unauthorized = false;
                    ths.setupProgress = 0;
                    var url = `moduleName=${ths.currentTab}`
                    ths.setupTabs = [];
                    dateService.GetProgresSetupStatusDetail(url).then(function(response){
                        if(response && response.data != null && response.data.status == "Success"){
                            var data = response.data.result;
                            var weightage = Math.ceil((100 / data.length));
                            data.forEach(function(item){
                                ths.setupTabs.push({
                                    tabCode: item.COMPONENT_NAME, 
                                    componentName: item.COMPONENT_NAME, 
                                    displayName: item.DISPLAY_NAME,
                                    icon: item.ICON,
                                    isCompleted: item.IS_COMPLETED
                                });
                                if(item.IS_COMPLETED == true){
                                    if((ths.setupProgress + weightage) < 100){
                                        ths.setupProgress = + ths.setupProgress + weightage;
                                    } else{
                                        ths.setupProgress = 100;
                                    }
                                }
                            })
                            ths.isLoading=false;
                        }
                    });
                } else {
                    ths.unauthorized = true;
                    ths.isLoading=false;
                }
            });
        },
        CloseOpenSetupModule: function(){
            this.isOpenSetupModule = !this.isOpenSetupModule;
        },
        CloseOpenConfigPopup: function(){
            this.isOpenConfiguration = !this.isOpenConfiguration;
        },
        GetMoreSettings: function(){    
                    
            var ths = this;
            GetMoreSettingsArray(ths.currentTab).then(function(response){                
                ths.moreSettings = response;
            });  
        },
        SetCurrentTab: function(moduleName, displayName) {    
                    
            this.currentTab = moduleName.toLowerCase();
            this.moduleDisplayName = displayName;
            this.GetMoreSettings();
            history.pushState(null, '', '/AdvanceSetup/'+ moduleName);
            this.GetProgresSetupStatusDetail();
        },
        GetModuleList: function() {
            var ths = this;
            var url =`userTypeId=${ths.userTypeId}&isForAdvanceSetup=true`
            dateService.GetModuleList(url).then(function (response) {              
                  if(response && response != null && response.data != null){
                    var modulelist = response.data.Data;                    
                    modulelist.forEach(function(x){                       
                        x.isVisible = true;
                    });
                    ths.moduleList = modulelist;
                }
                if (!ths.isBlank(ths.$route.params.moduleCode) && ths.$route.params.moduleCode.toLowerCase() !='index') {
                    var getModuleObj = ths.moduleList.filter((x) => { return x.module_name.toLowerCase() == ths.$route.params.moduleCode.toLowerCase()})
                    ths.currentTab = (ths.$route.params.moduleCode).toLowerCase();
                    ths.moduleDisplayName = getModuleObj[0].module_name
                } else {
                    ths.currentTab = ths.moduleList[0].module_name.toLowerCase();
                    ths.moduleDisplayName = ths.moduleList[0].module_name;
                }
                ths.GetMoreSettings();
                ths.GetProgresSetupStatusDetail();
                ths.isLoading = false;
            });
        },
        UpdateProgress: function(index, status){
            this.setupTabs[(index-1)].isCompleted = true;
        },
        RedirectTo:function(ths){ 
            if(ths.app == "Current"){                
                var baseDomain = `${ths.redirectTo}?reqUrl=${this.currentUrl}`;              ;
                this.$router.push({ path: `${baseDomain}` });                
            }else{                                
                window.location.href = `${process.env.VUE_APP_DEFAULT_APP_URL}${ths.redirectTo}?reqUrl=${this.currentUrl}`;                
            }
        }
    },
}
</script>